<div class="reset-password"><h2 class="mb-3">Reset Password</h2><form @submit.prevent="onSubmit"><div class="col-md-6 col-lg-4 col-xl-3 mx-auto"><div class="mb-3"><input class="form-control" :class="{'is-invalid': !!error.validation.password}" v-model="password" type="password" name="password" placeholder="Password" autocomplete="off" required><div class="invalid-feedback text-start" v-if="error.validation.password"><template v-for="message in error.validation.password">{{ message }}<br></template></div></div><div class="mb-3"><input class="form-control" v-model="passwordConfirmation" type="password" name="password_confirmation" placeholder="Password Confirmation" autocomplete="off" required></div><button class="btn btn-primary" :disabled="loading._" type="submit">Submit</button></div></form></div>